.bphpPromo {
	.countdown {
		text-align: center;
		display: inline-flex;
		align-items: center;
		gap: 8px;
		padding: 0;
		margin: auto 0;

		div {
			display: inline-block;
			vertical-align: baseline;
			width: 100%;
			margin: 0;

			display: flex;
			align-items: center;
			gap: 4px;
		}

		.days,
		.hours,
		.minutes,
		.seconds {
			text-align: center;
			min-width: 42px;
			width: auto;
			height: auto;
			display: inline-block;
			vertical-align: middle;
			line-height: 37px;
			box-sizing: border-box;
			border-radius: 3px;
			padding: 0 4px 0 3px;
		}

		.days_ref,
		.hours_ref,
		.minutes_ref,
		.seconds_ref {
			font-size: 16px;
			vertical-align: middle;
			text-transform: capitalize;
		}
	}
}