.bphpPromo {
	width: 100%;
	height: auto;
	box-shadow: 0 5px 10px #767676;
	z-index: 99999;

	.bphpPromoInner {
		height: auto;
		text-align: center;
		color: #fff;
		display: flex;
		align-items: center;
		justify-content: center;
		gap: 20px;
		flex-wrap: wrap;
		position: relative;
		padding: 5px;

		span.promo_txt {
			line-height: 100%;
			vertical-align: text-bottom;
		}

		.promo_btn {
			text-decoration: none !important;
			color: #fff;
			padding: 6px 15px;
			vertical-align: middle;
			display: inline-block;
		}

		#close {
			width: 16px;
			height: 16px;
			line-height: 16px;
			position: absolute;
			right: 20px;
			color: #f2f2f2;
			cursor: pointer;
		}
	}
}

@media only screen and (max-width: 576px) {
	.bphpPromo {
		.bphpPromoInner {
			span.promo_txt {
				display: inline-block;
				width: 100%;
			}

			#close {
				bottom: 20px;
			}
		}
	}
}